* {
    margin: 0;
    padding: 0;
  }
  
  html,
  body {
    width: 100%;
    height: 100%;

    overflow: hidden;

    background: #3f4c6b;
    background: linear-gradient(to bottom,  #303038 0%,#303838 50%,#101818 50, #101018 100%);
    

  }
  
  img:hover {
    /* Start the shake animation and make the animation last for 0.5 seconds */
    animation: shake 0.8s;

    /* When the animation is finished, start again */
    animation-iteration-count: infinite;
}

.title {
    -webkit-text-stroke: 0.25rem #44444450;
    color: #ffffff;
    font-family: 'Rubik Mono One', sans-serif;
    letter-spacing: .15rem;
    /* letter-spacing: 0.1rem; */
    text-align: center;
    text-shadow: 0px 4px 4px #00000040;
    padding-bottom: 2rem;
}

.plasma {
    color: transparent;
    background: linear-gradient(217deg, rgba(255, 0, 0, .8), rgba(255, 0, 0, 0) 70.71%),
        linear-gradient(127deg, rgba(0, 255, 0, .8), rgba(0, 255, 0, 0) 40.71%),
        linear-gradient(336deg, rgba(0, 0, 255, .8), rgba(0, 0, 255, 0) 70.71%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

@keyframes shake {
    0% {
        transform: translate(1px, 1px) rotate(0deg) scale(1, 1);
    }

    10% {
        transform: translate(-1px, -2px) rotate(-1deg) scale(1.1, 1.1);
    }

    20% {
        transform: translate(-3px, 0px) rotate(1deg) scale(1, 1);
    }

    30% {
        transform: translate(3px, 2px) rotate(0deg) scale(1.1, 1.1);
    }

    40% {
        transform: translate(1px, -1px) rotate(1deg) scale(1, 1);
    }

    50% {
        transform: translate(-1px, 2px) rotate(-1deg) scale(1, 1);
    }

    60% {
        transform: translate(-3px, 1px) rotate(0deg) scale(1.1, 1.1);
    }

    70% {
        transform: translate(3px, 1px) rotate(-1deg) scale(1, 1);
    }

    80% {
        transform: translate(-1px, -1px) rotate(1deg) scale(1.1, 1.1);
    }

    90% {
        transform: translate(1px, 2px) rotate(0deg) scale(1, 1);
    }

    100% {
        transform: translate(1px, -2px) rotate(-1deg) scale(1.1, 1.1);
    }
}


        /* Portrait green */
        @media screen and (orientation:portrait) {

          /* Portrait styles */
          #textoverlay {
              position: fixed;
              /* Sit on top of the page content */
              padding: 1vw 1vw 1vw 1vw;
              top: 50%;
              left: 25%;
              width: 51vw;
              /*height: 50%;*/
              transform: translate(0%, -50%);
              background-color: rgba(0, 0, 0, 0.25);
              z-index: 2;
              border-radius: 10px;
              /*border: 3px solid rgba(128,128,128,0.5);*/
              overflow: hidden;
              word-break: break-all;
              text-shadow: 0 5.36px 5.896px rgba(0, 0, 0, 1);
              font-family: 'Rubik Mono One', sans-serif;
              font-size: 3vw;
              color: rgb(90, 228, 80);
              user-select: none;
          }
      }

